// stream  player
import { DEFAULT_PLAYER_CONFIG } from "../common/constants";

class Stream {
  constructor(options) {
    Object.assign(this, DEFAULT_PLAYER_CONFIG, {
      userID: '',
      // stream userID
      streamType: '',
      // stream  [main small] aux
      streamID: '',
      // userID + '_' + streamType
      isVisible: true,
      // Q puserplayer iOS player
      hasVideo: false,
      hasAudio: false,
      volume: 0,
      //  0100
      playerContext: undefined // playerContext component context

    }, options);
  }

  setProperty(options) {
    Object.assign(this, options);
  }

  reset() {
    if (this.playerContext) {
      this.playerContext.stop();
      this.playerContext = undefined;
    }

    Object.assign(this, DEFAULT_PLAYER_CONFIG, {
      userID: '',
      // stream userID
      streamType: '',
      // stream  [main small] aux
      streamID: '',
      isVisible: true,
      hasVideo: false,
      hasAudio: false,
      volume: 0,
      //  0100
      playerContext: undefined
    });
  }

}

export default Stream;