import { DEFAULT_PUSHER_CONFIG } from "../common/constants";

class Pusher {
  constructor(options) {
    Object.assign(this, DEFAULT_PUSHER_CONFIG, {
      isVisible: true // Q puserplayer 

    }, options);
  }
  /**
   * wx.createLivePusherContext <live-pusher> context
   * @param {Object} context 
   * @returns {Object} livepusher context
   */


  getPusherContext(context) {
    if (!this.pusherContext) {
      this.pusherContext = uni.createLivePusherContext(context);
    }

    return this.pusherContext;
  }

  reset() {
    console.log('Pusher reset', this.pusherContext);

    if (this.pusherContext) {
      // 2020/09/23 
      // detachedpuhserContext.stop()url
      // stopurl
      this.pusherContext.stop({
        success: () => {
          console.log('Pusher pusherContext.stop()');
          Object.assign(this, DEFAULT_PUSHER_CONFIG, {
            isVisible: true
          });
        }
      });
      this.pusherContext = null;
    }
  }

}

export default Pusher;